





















































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue'; // @ is an alias to /src

@Component({
  components: {
    CButton,
  },
  head: {
    title: {
      inner: '逆ポーランド記法',
    },
  },
})
export default class Rpn extends Vue {
  // Data
  private dataButtonText: string = '解答と処理';
  private dataFormula: string = '1 2+3 4*+5/';
  private dataProcesses: {
    preText: string,
    processingChar: string,
    postText: string,
    stacks: number[],
  }[] = [];
  private dataAnswer: string = '';

  // Methods
  private listenInputText($event: Event): void {
    this.dataFormula = (<HTMLInputElement>(<any>$event).target).value;
  }
  private solve(): void {
    this.dataProcesses.length = 0;
    this.dataAnswer = '';
    let stacks: number[] = [];
    let signs: string[] = this.analyzeFormula();
    for (let i: number = 0; i < signs.length; i++) {
      // Calc
      if (signs[i].match(/^[0-9]+$/)) {
        stacks.push(Number(signs[i]));
      } else {
        if (2 <= stacks.length) {
          let num2: number = <number>stacks.pop();
          let num1: number = <number>stacks.pop();
          switch (signs[i]) {
            case '+':
              stacks.push(num1 + num2);
              break;
            case '-':
              stacks.push(num1 - num2);
              break;
            case '*':
              stacks.push(num1 * num2);
              break;
            case '/':
              if (num2 === 0) {
                this.dataAnswer = 'エラーです。0除算が発生しました。';
              } else {
                stacks.push(num1 / num2);
              }
              break;
            case '^':
              stacks.push(num1 ** num2);
              break;
            case '%':
              if (num2 === 0) {
                this.dataAnswer = 'エラーです。0除算が発生しました。';
              } else {
                stacks.push(num1 % num2);
              }
              break;
          }
        } else {
          this.dataAnswer = '式が正しくありません。';
        }
      }
      if (this.dataAnswer !== '') {
        break;
      }
      let process: {
        preText: string,
        processingChar: string,
        postText: string,
        stacks: number[],
      } = {
        preText: 0 < i ? signs.slice(0, i).join(' ') : '',
        processingChar: signs[i],
        postText: i + 1 < signs.length ? signs.slice(i + 1).join(' ') : '',
        stacks: stacks.slice(0),
      }
      this.dataProcesses.push(process);
    }
    if (this.dataAnswer === '') {
      if (stacks.length === 1) {
        this.dataAnswer = String(stacks.pop());
      } else {
        this.dataAnswer = '式が正しくありません。';
      }
    }
  }
  private analyzeFormula(): string[] {
    let signs: string[] = [];
    let sign: string = '';
    for (let i: number = 0; i < this.dataFormula.length; i++) {
      let char: string = this.dataFormula.charAt(i);
      if (char.match(/[\+\-\*\/\^\%]/)) {
        if (sign !== '') {
          signs.push(sign);
          sign = '';
        } 
        signs.push(char);
      } else if (char.match(/[0-9]/)) {
        sign += char;
      } else {
        if (sign !== '') {
          signs.push(sign);
          sign = '';
        }
      }
    }
    return signs;
  }
}
